/* App.css */

*{margin: 0px; padding: 0px;}
ul,li,ol{list-style: none;}
body{
  font-family: '맑은 고딕',arial, sans-serif;
  font-size: 12px;
  color: #333;
  height: 100%;
  background-color: #eff3f7;
}
.load_img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%);
}
/* pc버전 */
@media screen and (min-width:768px) {
  .header {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 180px; height: 100vh;
  }

  .header > h1 {
    text-align: center;
    padding: 15px 0px;
  }
  main{
    position: absolute;
    left: 180px; top:0px;
    width: calc(width - 180px);
  }
  h2{
    text-align: center;
  }
}
/* 모바일버전 */
@media screen and (max-width:767px) {
  .header {
    position: absolute;
    width: 100%;
    top: 0px;
  }
  
    .header h1 {
      text-align: center;
    }
  
  }
  
.nav{
  text-align: center;
  margin-top: 20px;
}
.nav li{
  padding-top: 10px;
}
.nav a{
  text-decoration: none;
  font-size: 16px;
  padding: 5px 10px;
  color:#333
}
