/* movie.css */

.movie{
  width: 43%;
  background-color: #fff;
  padding:20px;
  box-shadow: 0px 0px 10px #ccc;
  display: inline-block;
  margin-right: 25px;
  margin-bottom: 30px;
  position: relative;
}
.movie img{
  float: left;
  max-width:150px;
  width: 100%;
  box-shadow: 0px 20px 20px -12px #515151;
  position: relative;
  top:-30px;
  margin-right: 30px;
  transition: .3s;
  perspective: 200px;
  
}

.movie img:hover{
  transform: perspective(200px) rotateY(10deg);
}
.movie_data{
  
}
.movie_title, .movie_year{
  font-weight: 300px;
}
.movie_title{
  font-size: 20px;
  margin-bottom: 5px;
  color: #2c2c2c;
}
/* 장르 서식 */
.movie_genres{
  display: flex;
  flex-wrap: wrap;
  margin:5px 0px;
}
.movie_genres_txt{
  margin-right: 10px;
  font-size: 14px;
}
/* year서식 */
.movie_year{
  margin-right: 10px;
  font-size: 14px;
}
